.SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 6.94rem;
  margin-bottom: 6.33rem;
}

.SearchBar input {
  width: 287px;
  padding: 0.88rem 0;
  border: 1px solid #fff;
  border-radius: 3px;
  margin-bottom: 2.22rem;
  color: #010c3f;
  text-align: center;
  font-size: 1rem;
}

.SearchBar input:focus {
  outline: none;
}

.SearchButton {
  cursor: pointer;
  width: 8.11rem;
  padding: 0.77rem 0;
  border-radius: 54px;
  background-color: #d4af37;
  text-align: center;
  font-size: 0.833rem;
  transition: background-color 0.25s;
  border: none;
  color: #fff;
  font-weight: 500;
  outline: none;
}

.SearchButton:hover {
  background-color: rgb(212, 175, 0, 0.8);
}
